.sidebar {
    float: right;
    width: 300px;
    //border: 1px solid rgba(76, 115, 137, 0.746);
    //border-radius: 5px;
}

.previous_posts {
    padding: 0 6px 10px;
    border-radius: 5px;
    border: 1px solid rgba(76, 115, 137, 0.746);
    background: $soft-black;
}

.previous_posts center {
    padding-top: 10px;
    padding-bottom: 10px;
}

.previous_posts_inner {
    border-radius: 3px;
    box-shadow: inset .2em .2em .5em #000;
    padding: 10px;
}

.previous_list {
    list-style: none;
}

.previous_list li {
    padding: 5px;
}

.previous_list span {
    float: right;
}