$border-color: rgba(76, 115, 137, 0.746);
$soft-black: #1b1b1b66;

* {
    margin: 0;
    padding: 0;
}

html, body {
    display: block;
    width: 100%;
    height: 100%;

    background: #000;
    color: #ccc;

    line-height: 1.5;
    font: 12px Arial, Verdana, Geneva, sans-serif;
}

blockquote {
    border-left: .25em solid #393e3f;
    color: #6a737d;
    padding: 0 1em;
}

a {
    color: #497cf5;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}