/* 
    Image formating for use within blog posts.
    Syntax for resizing kramdown image is as follows:

    ![image-alt-text](image-link){:class="post_img_#"}
*/

.post_img_100 {
    width: 100%;
    height: 100%;
    margin: auto;
    display: block;
}

.post_img_75 {
    width: 75%;
    height: 75%;
    margin: auto;
    display: block;
}

.post_img_50 {
    width: 50%;
    height: 50%;
    margin: auto;
    display: block;
}

.post_img_25 {
    width: 25%;
    height: 25%;
    margin: auto;
    display: block;
}

.post_img_15 {
    width: 15%;
    height: 15%;
    margin: auto;
    display: block;
}
