.header_body {
    width:100%;
    background-color: #1b1b1b;
}

.header_content {
    margin: auto;
    height:55px;
    width:960px;

    display: flex;
    justify-content: center;
    align-items: center;
}



/*******************************************************/
/* SEARCH BAR */

.search_body {
    margin-left: auto;
}

#search_input {
    padding-left: 2px;
}

#results_container {
    position: absolute;
    list-style: none;
    //margin-top: 13px;
    background: #1b1b1b;

    width: 172px;

    border-left: 1px solid rgba(76, 115, 137, 0.746);
    border-right: 1px solid rgba(76, 115, 137, 0.746);
    border-bottom: 1px solid rgba(76, 115, 137, 0.746);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

#results_container li {
    padding: 5px;
}

#results_container a {
    color: #497cf5;
    text-decoration: none;
}

#results_container li:hover {
    text-decoration: underline;
}